@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  :root {
    --header-height: 3rem;
    --header-bg: #007BDF;
    --body-bg: #ffffff;
    --footer-bg: #007BDF;
  }
  @media screen and (min-width: 960px) {
    :root {
      --header-height: 4rem;
    }
  }

  ::selection {
    color: white;
    background: #00CBFF;
  }

  strong {
    font-weight: 700;
  }

  body {
    margin: var(--header-height) 0 0 0;
    min-height: calc(100vh - var(--header-height));
    background: var(--body-bg);
    display: flex;
    flex-direction: column;
  }

  .main-header {
    background: var(--header-bg);
    min-height: var(--header-height);
    position: fixed;
    top: 0;
    width: 100%;
  }

  .main-container {
    width: 100%;
    max-width: 3500px;
    margin: 0 auto;
    padding: 0 15px;
  }
  @media screen and (min-width: 1630px) {
    .main-container {
      padding: 0 0;
    }
  }

  .main-footer {
    background: var(--footer-bg);
    color: #FFF;
    padding: 2rem 0;
    margin-top: auto;
  }

  :root {
    --header-color: #1F2024;
    --paragraph-color:#1F2024;
  }

  h1, h2, h3, h4, h5, h6 {
    color: var(--header-color);
    margin: 0;
  }

  body {
    font-family: "Inter";
  }

  h1 {
    font: normal normal 600 36px/43px Inter;
    letter-spacing: -0.72px;
  }
  @media screen and (min-width: 960px) {
    h1 {
      font: normal normal 600 51px/56px Inter;
      letter-spacing: -0.66px;
    }
  }

  h2 {
    font: normal normal 600 29px/35px Inter;
    letter-spacing: -0.58px;
  }
  @media screen and (min-width: 960px) {
    h2 {
      font: normal normal 600 41px/49px Inter;
      letter-spacing: -0.82px;
    }
  }

  h3 {
    font: normal normal 600 24px/29px Inter;
    letter-spacing: -0.48px;
  }
  @media screen and (min-width: 960px) {
    h3 {
      font: normal normal 600 27px/32px Inter;
      letter-spacing: -0.54px;
    }
  }

  h4 {
    font: normal normal 600 20px/28px Inter;
    letter-spacing: -0.4px;
  }
  @media screen and (min-width: 960px) {
    h4 {
      font: normal normal 600 23px/28px Inter;
      letter-spacing: -0.46px;
    }
  }

  h5 {
    font: normal normal 600 17px/27px Inter;
    letter-spacing: -0.34px;
  }
  @media screen and (min-width: 960px) {
    h5 {
      font: normal normal 600 19px/23px Inter;
      letter-spacing: -0.38px;
    }
  }

  h6 {
    font: normal normal 600 14px/25px Inter;
    letter-spacing: -0.28px;
  }
  @media screen and (min-width: 960px) {
    h6 {
      font: normal normal 600 15px/27px Inter;
      letter-spacing: -0.3px;
    }
  }

  p {
    font: normal normal normal 15px/27px Inter;
    letter-spacing: 0px;
    --paragraph-color:#1F2024;
  }
  @media screen and (min-width: 960px) {
    p {
      font: normal normal normal 16px/22px Inter;
      letter-spacing: 0px;
    }
  }

  a {
    text-decoration: none;
  }
  a:hover {
    text-decoration: underline;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  menu, ol, ul {
    list-style: disc;
    /* Restaura los estilos de lista predeterminados para `ol`, `disc` es usual para `ul` */
    margin: 1em 0;
    /* Valores comunes por defecto de margen para listas */
    padding: 0 0 0 40px;
    /* Valores comunes por defecto de relleno para listas, proporcionando espacio para marcadores de lista */
  }
}
@layer components {
  .core-module-style {
    color: green;
  }

  .internal-module-style {
    color: red;
  }
}